<template>
  <v-app>
    <MenuComponent
      :scale-percent="scalePercent"
      :active-tool="activeTool"
      @image-loaded="onImageLoaded"
      @scale-changed="onScaleChanged"
      @open-resize="showResize = true"
      @tool-changed="activeTool = $event"
    />

    <ResizeComponent
      v-model="showResize"
      :width="imageSrc?.width"
      :height="imageSrc?.height"
      @confirm="applyResize"
    />

    <CanvasComponent
      ref="canvas"
      :image-src="imageSrc"
      :scale-percent="scalePercent"
      :active-tool="activeTool"
      @pick-color="onPickColor"
    />

    <v-footer app height="32" color="grey-darken-4" class="white--text px-4 align-center justify-center">
      {{ statusText }}
    </v-footer>

    <v-footer
      v-if="activeTool === 'eyedropper'"
      app
      height="32"
      color="grey-darken-4"
      class="white--text px-2 d-flex align-center justify-space-between eyedropper-footer"
    >
      <div v-if="colorA" class="d-flex align-center overflow-hidden flex-grow-1">
        <div class="swatch-box" :style="{ background: rgbString(colorA) }" title="Цвет A"></div>
        
        <span class="coord-label mr-3">
          <strong>Цвет 1</strong> ({{ colorA.x }}, {{ colorA.y }})
        </span>

        <div class="d-flex space-group">
          <template v-for="space in ['RGB', 'XYZ', 'Lab', 'OKLch']" :key="space">
            <v-tooltip location="top" offset="10">
              <template #activator="{ props }">
                <span v-bind="props" class="space-val">
                  <span class="space-tag">{{ space }}</span>
                  {{ getSpaceValues(space, colorA) }}
                </span>
              </template>
              <div class="text-caption">
                <strong>{{ spaceInfo[space].title }}</strong><br>
                {{ spaceInfo[space].axes }}
              </div>
            </v-tooltip>
          </template>
        </div>
      </div>

      <div v-if="colorA && colorB">
        <v-chip size="small" color="#008FFF">
          Контраст: {{ contrastValue }}:1
        </v-chip>
      </div>

      <div v-if="colorB" class="d-flex align-center overflow-hidden flex-grow-1 justify-end">
        <div class="d-flex space-group">
          <template v-for="space in ['RGB', 'XYZ', 'Lab', 'OKLch']" :key="space">
            <v-tooltip location="top" offset="10">
              <template #activator="{ props }">
                <span v-bind="props" class="space-val">
                  <span class="space-tag">{{ space }}</span>
                  {{ getSpaceValues(space, colorB) }}
                </span>
              </template>
              <div class="text-caption">
                <strong>{{ spaceInfo[space].title }}</strong><br>
                {{ spaceInfo[space].axes }}
              </div>
            </v-tooltip>
          </template>
        </div>

        <span class="coord-label ml-3">
          ({{ colorB.x }}, {{ colorB.y }}) <strong>Цвет 2</strong>
        </span>

        <div class="swatch-box ml-2" :style="{ background: rgbString(colorB) }" title="Цвет B"></div>
      </div>
    </v-footer>
  </v-app>
</template>

<script>
import MenuComponent from "./components/MenuComponent.vue";
import CanvasComponent from "./components/CanvasComponent.vue";
import ResizeComponent from "./components/ResizeComponent.vue";

export default {
  name: "App",
  components: { MenuComponent, CanvasComponent, ResizeComponent },

  data() {
    return {
      imageSrc: null,
      scalePercent: 100,
      statusText: "Загрузите изображение",
      showResize: false,
      activeTool: "hand",
      colorA: null,
      colorB: null,
      spaceInfo: {
        RGB: { title: "RGB", axes: "Red, Green, Blue: 0-255." },
        XYZ: { title: "XYZ", axes: "X: ~0-95, Y (Яркость): 0-100, Z: ~0-108." },
        Lab: { title: "Lab", axes: "L (Светлота): 0-100, a: (Зел/Крас), b: (Син/Желт)." },
        OKLch: { title: "OKLch", axes: "L: 0-1, C (Насыщенность): 0-0.4, h (Тон): 0-360°." }
      }
    };
  },

  computed: {
    contrastValue() {
      if (!this.colorA || !this.colorB) return "0";
      return this.calculateContrast(this.colorA, this.colorB).toFixed(2);
    }
  },

  mounted() {
    window.addEventListener("keydown", this.handleGlobalKeyDown);
  },

  beforeUnmount() {
    window.removeEventListener("keydown", this.handleGlobalKeyDown);
  },

  methods: {
    handleGlobalKeyDown(e) {
      const key = e.key.toLowerCase();
      if (key === 'h') this.activeTool = 'hand';
      if (key === 'i') this.activeTool = 'eyedropper';
    },

    onImageLoaded(data) {
      this.imageSrc = data;
      this.statusText = `Размер: ${data.width}×${data.height} px | Глубина: ${data.depth} бит`;
    },

    onScaleChanged(value) {
      this.scalePercent = value;
    },

    applyResize(data) {
      this.$refs.canvas.resizeImage(data.width, data.height, data.interpolation);
    },

    onPickColor({ color, slot }) {
      if (slot === "A") {
        this.colorA = color;
      } else {
        this.colorB = color;
      }
    },

    calculateContrast(a, b) {
      if (!a || !b) return 0;
      
      const getL = (c) => {
        let r = c.r / 255, g = c.g / 255, b = c.b / 255;
        r = r <= 0.03928 ? r / 12.92 : Math.pow((r + 0.055) / 1.055, 2.4);
        g = g <= 0.03928 ? g / 12.92 : Math.pow((g + 0.055) / 1.055, 2.4);
        b = b <= 0.03928 ? b / 12.92 : Math.pow((b + 0.055) / 1.055, 2.4);
        return 0.2126 * r + 0.7152 * g + 0.0722 * b;
      };

      const l1 = getL(a);
      const l2 = getL(b);
      
      return (Math.max(l1, l2) + 0.05) / (Math.min(l1, l2) + 0.05);
    },

    rgbString(c) { return `rgb(${c.r}, ${c.g}, ${c.b})`; },

    getSpaceValues(space, color) {
      const { r, g, b } = color;
      // нормализация sRGB
      let rn = r / 255, gn = g / 255, bn = b / 255;
      // linear RGB для XYZ
      const toLin = (v) => v <= 0.04045 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, 2.4);
      const rl = toLin(rn), gl = toLin(gn), bl = toLin(bn);

      if (space === 'RGB') return `${r},${g},${b}`;

      // XYZ
      const x = (rl * 0.4124 + gl * 0.3576 + bl * 0.1805) * 100;
      const y = (rl * 0.2126 + gl * 0.7152 + bl * 0.0722) * 100;
      const z = (rl * 0.0193 + gl * 0.1192 + bl * 0.9505) * 100;
      if (space === 'XYZ') return `${x.toFixed(0)},${y.toFixed(0)},${z.toFixed(0)}`;

      // Lab
      const f = (t) => t > 0.008856 ? Math.pow(t, 1/3) : (7.787 * t) + (16/116);
      const L = (116 * f(y / 100)) - 16;
      const a = 500 * (f(x / 95.047) - f(y / 100));
      const b_coord = 200 * (f(y / 100) - f(z / 108.883));
      if (space === 'Lab') return `${L.toFixed(0)},${a.toFixed(0)},${b_coord.toFixed(0)}`;

      // OKLch
      const chroma = Math.sqrt(a * a + b_coord * b_coord);
      let hue = Math.atan2(b_coord, a) * (180 / Math.PI);
      if (hue < 0) hue += 360;
      if (space === 'OKLch') return `${(L/100).toFixed(2)},${(chroma/100).toFixed(2)},${hue.toFixed(0)}°`;
      
      return "";
    },
  }
};
</script>

<style scoped>
.swatch-box {
  width: 22px;
  height: 22px;
  min-width: 18px;
  border: 2px solid #fff;
  border-radius: 4px;
  box-shadow: 0 0 4px rgba(0,0,0,0.5);
  margin-right: 8px;
}

.coord-label {
  font-size: 11px;
  color: #eee;
  white-space: nowrap;
  padding-right: 1px 6px;
} 

.space-val {
  padding: 1px 4px;
}

.space-tag {
  color: #008FFF;
  font-weight: bold;
  font-size: 12px;
  margin-right: 3px;
  text-transform: uppercase;
}

.v-chip--size-x-small {
  padding: 0 6px;
  height: 18px;
}
</style>