<template>
  <v-app-bar app color="primary">
    <v-toolbar-title>Онлайн Фотошоп</v-toolbar-title>

    <v-tooltip text="Рука (H)" location="bottom">
      <template #activator="{ props }">
        <v-btn
          v-bind="props"
          icon
          :color="activeTool === 'hand' ? 'white' : 'white-darken-2'"
          :variant="activeTool === 'hand' ? 'tonal' : 'text'"
          @click="$emit('tool-changed', 'hand')"
        >
          <v-img src="https://cdn-icons-png.flaticon.com/512/658/658022.png" width="28px"></v-img>
        </v-btn>
      </template>
    </v-tooltip>

    <v-tooltip text="Пипетка (I)" location="bottom">
      <template #activator="{ props }">
        <v-btn
          v-bind="props"
          icon
          :color="activeTool === 'eyedropper' ? 'white' : 'white-darken-2'"
          :variant="activeTool === 'eyedropper' ? 'tonal' : 'text'"
          @click="$emit('tool-changed', 'eyedropper')"
        >
          <v-img src="https://cdn-icons-png.flaticon.com/512/4347/4347420.png" width="28px"></v-img>
        </v-btn>
      </template>
    </v-tooltip>

    <v-btn @click="triggerFileInput" class="mr-4">
      Загрузить изображение
    </v-btn>

    <v-btn class="ml-2" @click="$emit('open-resize')">
      Изменить размер
    </v-btn>

    <v-select
      :items="scales"
      :model-value="currentScale"
      label="Масштаб"
      density="compact"
      style="max-width: 120px"
      @update:modelValue="onScaleChange"
    />

    <input
      ref="fileInput"
      type="file"
      accept=".jpg,.jpeg,.png,.gb7"
      hidden
      @change="onFileChange"
    />
  </v-app-bar>
</template>

<script>
export default {
  name: "MenuComponent",

  emits: ["tool-changed"],

  props: ["scalePercent", "activeTool"],

  data() {
    return {
      scales: [12, 25, 50, 75, 100, 150, 200, 300],
      currentScale: 100,
      //activeTool: "hand"
    };
  },

  mounted() {
    window.addEventListener("keydown", e => {
      if (e.key.toLowerCase() === "h") this.setTool("hand");
      if (e.key.toLowerCase() === "i") this.setTool("eyedropper");
    });
  },

  beforeUnmount() {
    window.removeEventListener("keydown", this.onKeyDown);
  },

  watch: {
    scalePercent(val) {
      this.currentScale = val;
    }
  },

  methods: {
    setTool(tool) {
      this.$emit("tool-changed", tool);
    },

    onKeyDown(e) {
      const key = e.key.toLowerCase();

      if (key === "h") {
        this.setTool("hand");
      }

      if (key === "i") {
        this.setTool("eyedropper");
      }
    },

    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    onScaleChange(value) {
      this.currentScale = value;
      this.$emit("scale-changed", Number(value));
    },

    async onFileChange(event) {
      const file = event.target.files[0];
      if (!file) return;

      const ext = file.name.split(".").pop().toLowerCase();

      if (["jpg", "jpeg", "png"].includes(ext)) {
        const reader = new FileReader();
        reader.onload = e => {
          const img = new Image();
          img.onload = () => {
            this.$emit("image-loaded", {
              type: "image",
              src: e.target.result,
              width: img.width,
              height: img.height,
              depth: screen.colorDepth
            });
          };
          img.src = e.target.result;
        };
        reader.readAsDataURL(file);
      }

      else if (ext === "gb7") {
        const buffer = await file.arrayBuffer();
        const data = new DataView(buffer);

        // проверка сигнатуры GB7
        if (
          data.getUint8(0) !== 0x47 || // G
          data.getUint8(1) !== 0x42 || // B
          data.getUint8(2) !== 0x37 || // 7
          data.getUint8(3) !== 0x1d    // управляющий символ-разделитель групп
        ) {
          alert("Неверный формат GB7");
          return;
        }

        // заголовок
        // const version = data.getUint8(4);     // версия
        const flag = data.getUint8(5);           // флаг
        const width = data.getUint16(6, false);  // ширина
        const height = data.getUint16(8, false); // высота
        // резерв 10-11
        const pixels = new Uint8ClampedArray(width * height * 4); // RGBA
        let ptr = 12; // изображение с/без маски

        for (let i = 0; i < width * height; i++) {
          const byte = data.getUint8(ptr++);
          const gray = byte & 0x7f;        // 7 бит
          const mask = (byte & 0x80) >> 7; // бит маски
          const v = gray * 2;              // 7 бит растянем до 0–254

          pixels[i * 4] = v;
          pixels[i * 4 + 1] = v;
          pixels[i * 4 + 2] = v;
          pixels[i * 4 + 3] = flag && mask === 0 ? 0 : 255; // прозрачность
        }

        this.$emit("image-loaded", {
          type: "gb7",
          width,
          height,
          pixels,
          depth: 7
        });
      }
    }
  }
};
</script>

<style scoped>
.tool-btn {
  border: 2px solid transparent;
  border-radius: 8px;
}

.tool-btn.active {
  border-color: #fff;
}
</style>