<template>
  <div 
    class="canvas-container"
    ref="container"
    @mousedown="onMouseDown"
    @mousemove="onMouseMove"
    @mouseup="onMouseUp"
    @mouseleave="onMouseUp"
  >
    <canvas ref="canvas" @click="onClick" :style="canvasStyle"></canvas>
  </div>
</template>

<script>
export default {
  name: "CanvasComponent",

  props: {
    imageSrc: Object,
    scalePercent: Number,
    activeTool: String
  },

  emits: ["pick-color", "scale-changed"],

  data() {
    return {
      originalPixels: null,
      originalWidth: 0,
      originalHeight: 0,
      scale: 1,
      ctx: null,
      imageData: null,
      colorA: null,
      colorB: null,

      offsetX: 0,
      offsetY: 0,
      isDragging: false,
      lastMouseX: 0,
      lastMouseY: 0,
      viewPadding: 50
    };
  },

  computed: {
    canvasStyle() {
      return {
        transform: `translate(${this.offsetX}px, ${this.offsetY}px)`,
        cursor: this.activeTool === 'hand' 
          ? (this.isDragging ? 'grabbing' : 'grab') 
          : 'crosshair',
        transition: this.isDragging ? 'none' : 'transform 0.1s ease-out'
      };
    }
  },

  mounted() {
    window.addEventListener("keydown", this.onKeyDown);
  },

  beforeUnmount() {
    window.removeEventListener("keydown", this.onKeyDown);
  },

  watch: {
    imageSrc(newVal) {
      if (!newVal) return;

      if (newVal.type === "image") {
        this.loadStandardImage(newVal.src);
      } else if (newVal.type === "gb7") {
        this.loadGB7(newVal);
      }
    },

    scalePercent(newVal) {
      this.scale = newVal / 100;
      this.redraw();
    }
  },

  methods: {
    loadStandardImage(src) {
      const img = new Image();
      img.onload = () => {
        this.originalWidth = img.width;
        this.originalHeight = img.height;

        const off = document.createElement("canvas");
        off.width = img.width;
        off.height = img.height;

        const ctx = off.getContext("2d");
        ctx.drawImage(img, 0, 0);

        this.originalPixels = ctx.getImageData(
          0, 0, img.width, img.height
        ).data;

        this.fitToScreen();
      };
      img.src = src;
    },

    loadGB7({ width, height, pixels }) {
      this.originalWidth = width;
      this.originalHeight = height;
      this.originalPixels = pixels;
      this.fitToScreen();
    },

    resetPosition() {
      this.offsetX = 0;
      this.offsetY = 0;
    },

    constrainOffset(x, y) {
      const container = this.$refs.container;
      const canvas = this.$refs.canvas;
      if (!container || !canvas) return { x, y };

      const cw = container.clientWidth;
      const ch = container.clientHeight;
      const vw = canvas.width;
      const vh = canvas.height;

      const limitX = (vw / 2) + (cw / 2) - this.viewPadding;
      const limitY = (vh / 2) + (ch / 2) - this.viewPadding;

      return {
        x: Math.max(-limitX, Math.min(limitX, x)),
        y: Math.max(-limitY, Math.min(limitY, y))
      };
    },

    onMouseDown(e) {
      if (this.activeTool !== 'hand') return;
      this.isDragging = true;
      this.lastMouseX = e.clientX;
      this.lastMouseY = e.clientY;
    },

    onMouseMove(e) {
      if (!this.isDragging || this.activeTool !== 'hand') return;

      const dx = e.clientX - this.lastMouseX;
      const dy = e.clientY - this.lastMouseY;

      const newPos = this.constrainOffset(this.offsetX + dx, this.offsetY + dy);
      this.offsetX = newPos.x;
      this.offsetY = newPos.y;

      this.lastMouseX = e.clientX;
      this.lastMouseY = e.clientY;
    },

    onMouseUp() {
      this.isDragging = false;
    },

    onKeyDown(e) {
      if (this.activeTool !== 'hand') return;
      
      const step = e.shiftKey ? 50 : 10;
      let dx = 0;
      let dy = 0;

      if (e.key === "ArrowLeft") dx = -step;
      if (e.key === "ArrowRight") dx = step;
      if (e.key === "ArrowUp") dy = -step;
      if (e.key === "ArrowDown") dy = step;

      if (dx !== 0 || dy !== 0) {
        e.preventDefault();
        const newPos = this.constrainOffset(this.offsetX + dx, this.offsetY + dy);
        this.offsetX = newPos.x;
        this.offsetY = newPos.y;
      }
    },

    fitToScreen() {
      const container = this.$el;
      const maxW = container.clientWidth - 100;
      const maxH = container.clientHeight - 100;

      const scale = Math.min(
        maxW / this.originalWidth,
        maxH / this.originalHeight,
        1
      );

      this.scale = scale;
      this.$emit("scale-changed", Math.round(scale * 100));
      this.redraw();
    },

    redraw() {
      if (!this.originalPixels) return;

      const dstW = Math.round(this.originalWidth * this.scale);
      const dstH = Math.round(this.originalHeight * this.scale);

      const canvas = this.$refs.canvas;
      canvas.width = dstW;
      canvas.height = dstH;

      const pixels = this.bilinear(
        this.originalPixels,
        this.originalWidth,
        this.originalHeight,
        dstW,
        dstH
      );
      
      const ctx = canvas.getContext("2d");
      // сохраняем в data для доступа пипетки
      this.imageData = new ImageData(pixels, dstW, dstH); 
      ctx.putImageData(this.imageData, 0, 0);

      const validated = this.constrainOffset(this.offsetX, this.offsetY);
      this.offsetX = validated.x;
      this.offsetY = validated.y;
    },

    bilinear(src, sw, sh, dw, dh) {
      const dst = new Uint8ClampedArray(dw * dh * 4);

      for (let y = 0; y < dh; y++) {
        for (let x = 0; x < dw; x++) {
          const gx = (x + 0.5) * sw / dw - 0.5;
          const gy = (y + 0.5) * sh / dh - 0.5;

          const x0 = Math.floor(gx);
          const y0 = Math.floor(gy);
          const x1 = Math.min(x0 + 1, sw - 1);
          const y1 = Math.min(y0 + 1, sh - 1);

          const dx = gx - x0;
          const dy = gy - y0;

          for (let c = 0; c < 4; c++) {
            const i00 = (y0 * sw + x0) * 4 + c;
            const i10 = (y0 * sw + x1) * 4 + c;
            const i01 = (y1 * sw + x0) * 4 + c;
            const i11 = (y1 * sw + x1) * 4 + c;

            dst[(y * dw + x) * 4 + c] =
              src[i00] * (1 - dx) * (1 - dy) +
              src[i10] * dx * (1 - dy) +
              src[i01] * (1 - dx) * dy +
              src[i11] * dx * dy;
          }
        }
      }
      return dst;
    },

    nearest(srcData, srcW, srcH, dstW, dstH) {
      const dst = new Uint8ClampedArray(dstW * dstH * 4);

      for (let y = 0; y < dstH; y++) {
        for (let x = 0; x < dstW; x++) {
          const sx = Math.floor(x * srcW / dstW);
          const sy = Math.floor(y * srcH / dstH);
          const sIdx = (sy * srcW + sx) * 4;
          const dIdx = (y * dstW + x) * 4;
          dst.set(srcData.slice(sIdx, sIdx + 4), dIdx);
        }
      }
      return dst;
    },

    resizeImage(newW, newH, method) {
      const algo =
        method === "nearest"
          ? this.nearest
          : this.bilinear;

      const pixels = algo(
        this.originalPixels,
        this.originalWidth,
        this.originalHeight,
        newW,
        newH
      );

      this.originalPixels = pixels;
      this.originalWidth = newW;
      this.originalHeight = newH;

      this.scale = 1;
      this.redraw();
    },

    onClick(e) {
      if (this.activeTool !== "eyedropper") return;
      if (!this.imageData) return;

      // предотвращаем стандартные действия браузера
      e.preventDefault();

      const rect = this.$refs.canvas.getBoundingClientRect();
      const x = Math.floor(e.clientX - rect.left);
      const y = Math.floor(e.clientY - rect.top);

      // проверка на выход за границы
      if (x < 0 || y < 0 || x >= this.imageData.width || y >= this.imageData.height) return;

      const i = (y * this.imageData.width + x) * 4;
      const color = {
        r: this.imageData.data[i],
        g: this.imageData.data[i + 1],
        b: this.imageData.data[i + 2],
        x,
        y
      };

      // проверка нажатия любой из клавиш
      const isSecondColor = e.altKey || e.ctrlKey || e.shiftKey;
      const slot = isSecondColor ? "B" : "A";

      console.log(`Picked color for slot ${slot}:`, color);
      this.$emit("pick-color", { color, slot });
    }
  }
};
</script>

<style scoped>
.canvas-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 64px - 32px);
  background: #fff;
  margin-top: 64px;
  overflow: hidden;
  position: relative;
}
canvas {
  display: block;
  user-select: none;
  touch-action: none;
}
</style>